

.wrapper{
    display: flex;
    overflow: hidden;
    flex-direction: column;
    height: 100vh;
    font-family: "'Montserrat', sans-serif'";
}

.headText{
    color: #005475;
    font-size: 20px;
    font-weight: 600;

}

.sectionName{
    color: #005475;
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
}



.body{
    flex: 1 1 auto;
    /* max-height: calc(100vh - 173px); */
    margin-top: 5px;
    overflow: hidden;
    /* border: 1px solid red; */
}

.bodyContainer{
        display: flex;
        /* width: 100vw; */
        height: 100%; 
}

.left{
    width: 45vw;
    font-size: 1.4em;
    justify-content: center;
    display: flex;
    color: #005475;
    padding-left: 0.5em;
    border-right: 1px solid #005475;
    height: 98%;
}

.titleStrategy{
    padding: 5px;
    font-size: 1.4rem;
}

.selectList{
    overflow-x: auto;
    overflow-y: hidden;
    font-size: 1.3em;

    li{
        display: flex;
        /* width: 60vw; */
        padding: 0.4rem 0.8rem;
        justify-content: flex-start;

        div {
            padding-left: 5px;
        }
    }
}

.input1{
    display: flex;
    width: 90%;
    height: 59%;
    box-shadow: 0 0 1px black;
    margin-top: 10px;
}

.right{
    height: 98%;
    overflow: auto;
    width: 55vw;
}


.inputContainer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: white;
    border-top: #005475 solid 3px;
}


.inputRow1{
    display: flex;
    margin-top: 57px;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    padding: 0 1rem;


    div{
        display: flex;
        flex: 1 0 auto;
        align-items: center;
    }

    input{
        padding: 0 1em;
        height: 1.5rem;
        width: 12rem;
        font-size: 16px;
        text-transform: uppercase;
        border-bottom: 1px solid #005475;
    }
    @media screen and (max-width: 375px) {
        input {
            width: 7rem;
        }
    }
    @media screen and (max-width: 440px) {
        input {
            width: 10rem;
        }
    }

    img{
        display: inline-block;
        border-bottom: #005475 1px solid;
        height: 1.5rem;

    }

    select{
        display: inline-block;
        font-family: "'Montserrat', sans-serif";
        font-size: 16px;
        height: 1.5rem;
        border-bottom: #005475 1px solid;
    }
}

.first{
    justify-content: space-between;
    padding: 1rem;
}

.second{
    justify-content: center;
}

.inputColumn{
    display: flex;
    /*border: black solid 1px;*/
}
.inputRow2{
    display: flex;
    padding: 1rem 2.5rem;
    /*border: #005475 1px solid;*/
    width: 100%;
    align-items: center;

    @media screen and (max-width: 395px) {
        padding: 1.5rem 2rem;
    }
    @media screen and (max-width: 370px) {
        padding: 1.5rem 1.5rem;
    }
    @media screen and (max-width: 350px) {
        padding: 1.5rem 0.5rem;
    }


    img{
        display: inline-block;
        width: 30px;
        height: 30px;
        box-shadow: 0 0 3px black;
        border-radius: 10px;

    }

    button{
        height: 2rem;
        width: 11rem;
        background: #005475;
        color: white;
        font-size: 16px;
        border: #005475 1px solid;
        border-radius: 5px;

    }

    div{
        display: flex;
        flex: 1 1 auto;
        /*border: 1px solid #005475;*/
        justify-content: center;
    }
}

.image{
    margin: 0 1rem;

}

.addDraft{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 150px;
    margin: 0.4rem 10px;
    padding: 5px 7px;
    /* font-size: 1rem; */
    border-radius: 15px;
    box-shadow: 0 0 2px black;
}

