.wrapper{
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
}

.dialog {
    width: 75%;
    border-radius: 0px 8px 8px 0px;
    display: flex;
    flex-direction: column;
    background-color: #ffff;
    box-shadow: 0px 0px 3px 0 rgba(0, 0, 0, 0.5);
}


.main {
    position: relative;
    flex: 1 0 120px;
    overflow-y: auto;
    background-color: #F0F0F0;
    /* padding-top: 1%; */
    /* padding-bottom: 1%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    /* row-gap: 1%; */
}