.content {
    padding: 1%;
}

.titleContainer {
    >input {
        display: block;

        width: 95%;

        margin: 0 auto;

        border-bottom: 1px solid #bfbfbf;

        font-size: 1rem;
    }
}

.postContainer{
    position: relative;
    max-width: 95%;
    margin: 0 auto 10px auto;
    border: 1px solid white;
    >select {
        display: block;

        width: 100%;

        margin: 0 auto;

        text-align: center;

        border-bottom: #bfbfbf 1px solid;
    }
}

.postContainer::before{
    content: "Пост";
    color: rgb(58, 58, 58);
    position: absolute;
    top: -6px;
    left: 8px;
    pointer-events: none;
    font-weight: 500;
    font-size: 12px;
    font-style: italic;
    z-index: 1000;
}

.dateContainer {


    >input {
        position: relative;
        display: block;

        width: 95%;

        padding: 7px 0 0 0;
        margin: 3px auto;

        border-bottom: 1px solid #bfbfbf;

        font-size: 1rem;
        text-align: center;
    }

    >input::before {
        content: "Дата завершения";
        color: rgb(58, 58, 58);
        position: absolute;
        top: -2px;
        left: 8px;
        pointer-events: none;
        font-weight: 500;
        font-size: 12px;
        font-style: italic;
        z-index: 1000;
    }

    >input:nth-child(1)::before {
        content: "Начало исполнения";
    }
}


.descriptionContainer {
    padding: 10px 0;

    >textarea {
        display: block;

        width: 95%;
        min-height: 50px;
        max-height: 280px;

        padding: 5px;
        margin: 0 auto;

        border: 1px solid #bfbfbf;
        border-radius: 15px;

        resize: none;
    }
}

.stateContainer{
    position: relative;

    max-width: 95%;
    margin: 0 auto 10px auto;

    >select {
        display: block;

        width: 100%;

        margin: 0 auto;

        text-align: center;

        border-bottom: #bfbfbf 1px solid;
    }
}

.stateContainer::before{
    content: "Статус";
    color: rgb(58, 58, 58);
    position: absolute;
    top: -6px;
    left: 8px;
    pointer-events: none;
    font-weight: 500;
    font-size: 12px;
    font-style: italic;
    z-index: 1000;
}


.attachContainer {
    position: relative;

    max-width: 95%;
    margin: 0 auto;

    >select {
        display: block;

        width: 100%;

        margin: 0 auto;

        text-align: center;

        border-bottom: #bfbfbf 1px solid;
    }

    >input {
        display: block;

        width: 95%;

        padding: 10px 0 5px 0;
        margin: 0 auto;

    }
}

.attachContainer::before{
    content: "Политика";
    color: rgb(58, 58, 58);
    position: absolute;
    top: -7px;
    left: 8px;
    pointer-events: none;
    font-weight: 500;
    font-size: 12px;
    font-style: italic;
    z-index: 1000;
}

.attachmentsContainer {
    margin-top: 20px;
}

.attachmentsContainer ul {
    list-style-type: none;
    padding: 0;
}

.attachmentsContainer li {
    margin-bottom: 15px;
    border: 1px solid #ddd;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.attachmentsContainer img {
    max-width: 200px;
    height: auto;
}


.customFileButton {
    background: linear-gradient(to bottom, #FFFFFF, #E6E6E6) #F5F5F5 repeat-x;
    border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) #B3B3B3;
    border-radius: 4px;
    border-style: solid;
    border-width: 1px;
    box-shadow: 0 1px 0 rgba(255, 255, 255, 0.2) inset, 0 1px 2px rgba(0, 0, 0, 0.05);
    color: #333333;
    text-decoration: none;
    display: block;
    font-size: 16px;
    width: 95%;
    line-height: 20px;
    margin: 20px auto;
    padding: 4px 12px;
    text-align: center;
    text-shadow: 0 1px 1px rgba(255, 255, 255, 0.75);
    vertical-align: middle;
    position: relative;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-property: -webkit-transform;
    transition-property: transform;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

.customFileButton:before {
    pointer-events: none;
    position: absolute;
    z-index: -1;
    content: '';
    top: 100%;
    left: 5%;
    height: 10px;
    width: 90%;
    opacity: 0;
    background: -webkit-radial-gradient(center, ellipse, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0) 80%);
    background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0) 80%);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-property: -webkit-transform, opacity;
    transition-property: transform, opacity;
}

@media(hover: hover) {

    .customFileButton:hover {
        -webkit-transform: translateY(-5px);
        -ms-transform: translateY(-5px);
        transform: translateY(-5px);
    }
    
    .customFileButton:hover:before {
        opacity: 1;
        -webkit-transform: translateY(5px);
        -ms-transform: translateY(5px);
        transform: translateY(5px);
    }
}

@media(hover: none){
    .customFileButton:active {
        -webkit-transform: translateY(-5px);
        -ms-transform: translateY(-5px);
        transform: translateY(-5px);
    }
    
    .customFileButton:active:before {
        opacity: 1;
        -webkit-transform: translateY(5px);
        -ms-transform: translateY(5px);
        transform: translateY(5px);
    }
}


/* Стили для контейнера выбранных файлов */
.selectedFiles {
    margin: 0 auto 10px auto;
    width: 95%;
    max-height: 200px;
    overflow-y: auto;
    /* Добавляем прокрутку, если файлов слишком много */
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 8px;
    background-color: #f9f9f9;
}

/* Стили для каждого элемента файла */
.fileItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
    margin-bottom: 8px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.imagePreview {
    max-width: 100px; /* Максимальная ширина превью */
    max-height: 100px; /* Максимальная высота превью */
    border-radius: 4px; /* Скругление углов */
    object-fit: cover; /* Сохраняет пропорции изображения */
    margin-right: 8px; /* Отступ от текста или кнопки */
}

/* Стили для текста с именем файла */
.fileItem span {
    font-size: 14px;
    color: #333;
    flex-grow: 1;
    /* Чтобы текст занимал все доступное пространство */
    margin-right: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

/* Стили для кнопки удаления */
.fileItem button {
    background-color: #ff4d4f;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 4px 8px;
    font-size: 12px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* Эффект при наведении на кнопку удаления */
.fileItem button:hover {
    background-color: #ff7875;
}

/* Эффект при нажатии на кнопку удаления */
.fileItem button:active {
    background-color: #d9363e;
}